.wrapper {
  position: relative;
  min-height: 100%;
  height: auto !important;
  overflow-x: hidden;
}

.boxed-layout .container-fluid, .boxed-layout-example .container-fluid {
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
}

.container-fluid {
  margin: 0;
  padding: 20px 20px 0;
  position: relative;
  min-height: 100%;
  height: calc(100% - 80px) !important;
}

.container-fluid .page-title {
  font-size: 22px;
  color: #333;
  padding: 0 15px;
  margin-bottom: 20px;
  display: inline-block;
}

.page-footer {
  height: 80px;
  text-align: center;
  line-height: 80px;
}

.content-box {
  position: relative;
  padding: 0;
  margin-bottom: 20px;
  background: #fff;
  border-radius: 3px;
  box-shadow: 0px 1px 2px 0px #dadada;
}

.content-box.is-fullscreen {
  width: 100%;
  height: 100%;
  border-radius: 0;
  margin: 0;
  overflow-y: auto;
}

.content-box .head {
  padding: 0 20px;
  max-height: 50px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.content-box .head.head-border {
  border-top: 3px solid #333;
}

.content-box .head.head-with-btns {
  max-height: inherit;
}

.content-box .head.head-with-btns .functions-btns {
  max-height: inherit;
}

@media (max-width: 768px) {
  .content-box .head.head-with-btns .functions-btns {
    line-height: 1;
  }
  .content-box .head.head-with-btns .functions-btns button {
    margin-bottom: 5px;
  }
}

.content-box .head.head-with-btns .content-title {
  width: calc(100% - 350px);
}

@media (max-width: 768px) {
  .content-box .head.head-with-btns .content-title {
    width: 100%;
  }
}

.content-box .head.primary-color {
  border-color: #aa66cc;
}

.content-box .head.success-color {
  border-color: #99cc00;
}

.content-box .head.warning-color {
  border-color: #ffbb33;
}

.content-box .head.info-color {
  border-color: #49ceff;
}

.content-box .head.danger-color {
  border-color: #ff5f5f;
}

.content-box .content-title {
  text-transform: uppercase;
  margin: 0;
  font-weight: 500;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: calc(100% - 100px);
  overflow: hidden;
  line-height: 50px;
  color: #fff;
}

.content-box .functions-btns {
  line-height: 50px;
  max-height: 50px;
  margin: 0;
  padding: 0;
}

.content-box .functions-btns > a, .content-box .functions-btns .dropdown-toggle {
  display: inline-block;
  margin-left: 10px;
  color: #fff;
  font-size: 16px;
  position: relative;
  z-index: 1;
}

.content-box .functions-btns > a:before, .content-box .functions-btns .dropdown-toggle:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 14px;
  left: -5px;
  transition: all 250ms;
  transform: scale(0);
  z-index: -1;
}

.content-box .functions-btns > a:hover:before, .content-box .functions-btns .dropdown-toggle:hover:before {
  transform: scale(1);
}

.content-box .content {
  padding: 20px;
}

.content-box .dataTables_wrapper {
  box-shadow: none;
  border-radius: 0;
  margin: 0;
}

.box-drop .dropdown-toggle {
  font-size: 16px;
  cursor: pointer;
  line-height: 1;
  padding: 0 5px;
  color: #333 !important;
  display: inline-block;
  position: relative;
  z-index: 1;
}

.box-drop .dropdown-toggle:before {
  width: 21px !important;
  height: 21px !important;
  top: -2px !important;
  left: -4px !important;
}

.box-drop .open .dropdown-toggle {
  box-shadow: none;
}

.box-drop .open .dropdown-toggle:before {
  transform: scale(1);
}

.box-drop .dropdown-menu {
  top: 40px;
}

.content-scroll {
  height: 250px;
}

#configuration {
  top: 130px;
  right: 0;
  overflow: hidden;
  position: absolute;
}

.mdl-switch {
  width: auto;
}

.conf-box {
  z-index: 8;
  position: relative;
  transition-duration: 0.4s;
  margin-right: -270px;
}

.conf-box.active {
  margin-right: 0 !important;
}

.conf-body {
  background: #F3F3F3;
  padding: 10px;
  margin-left: 50px;
  width: 270px;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.23), 0 3px 12px rgba(0, 0, 0, 0.16);
}

.conf-body .primary-colors a, .conf-body .secondary-colors a, .conf-body .nav-colors a {
  width: 47px;
  height: 47px;
  display: inline-block;
  position: relative;
}

.conf-body .primary-colors a.selected:after, .conf-body .secondary-colors a.selected:after, .conf-body .nav-colors a.selected:after {
  content: "\f26b";
  font-family: "Material-Design-Iconic-Font";
  color: #fff;
  background: rgba(0, 0, 0, 0.2);
  line-height: 47px;
  width: 47px;
  text-align: center;
  font-size: 30px;
  display: inline-block;
  position: absolute;
}

.conf-body .title {
  margin-top: 10px;
}

